}

.bio-properties-panel-feel-icon {
  display: inline-block;
  height: 16px;
  border-radius: 8px;
  vertical-align: text-bottom;
  padding: 0;
  margin: 0 3px;
  align-items: center;
  align-self: center;
  border: none;
  background: none;
}

.bio-properties-panel-feel-icon.optional {
